function start() {
	var angularApp = angular.module('angularApp', [
		'ngResource', 
		'ngAnimate', 
		'perfect_scrollbar', 
		'cartCtrl', 
		'resourcesService'
	], function($interpolateProvider) {
	    $interpolateProvider.startSymbol('<%');
	    $interpolateProvider.endSymbol('%>');
	})
}
start();